import React, { useState } from "react";
import Slider, { Settings } from "react-slick";
import { ValuesData } from "../../../models";
import classNames from "classnames";
import "./carousel.less";

interface CarouselProps {
	data: ValuesData;
}

const Carousel: React.FC<CarouselProps> = ({ data }) => {
	const [italic, setItalic] = useState<boolean>(false);
	const [bold, setBold] = useState<boolean>(false);
	const [dropShadow, setDropShadow] = useState<boolean>(false);
	const [background, setBackground] = useState<boolean>(false);
	const [border, setBorder] = useState<boolean>(false);
	const [hideCheckboxes, setHideCheckboxes] = useState<boolean>(false);

	const settings: Settings = {
		dots: true,
		infinite: true,
		speed: 750,
		slidesToShow: 1,
		slidesToScroll: 1,
		autoplay: true,
		autoplaySpeed: 5000,
		arrows: true,
		pauseOnHover: true,
		adaptiveHeight: true,  // Ensure height adapts to content
		variableWidth: false,
	};

	const handleCheckboxChange = (event: React.ChangeEvent<HTMLInputElement>) => {
		const { name, checked } = event.target;

		switch (name) {
			case "italic":
				setItalic(checked);
				break;
			case "bold":
				setBold(checked);
				break;
			case "border":
				setBorder(checked);
				break;
			case "background":
				setBackground(checked);
				break;
			case "dropShadow":
				setDropShadow(checked);
				break;
			case "hideCheckboxes":
				setHideCheckboxes(checked);
				break;
			default:
				break;
		}
	};

	const containerClassName = classNames("carousel-container", {
		border,               // Adds "border" class if border is true
		background,           // Adds "background" class if background is true
		dropShadow,            // Adds "dropShadow" class if dropShadow is true
	});

	const textClassName = classNames({
		bold,               // Adds "border" class if border is true
		italic,           // Adds "background" class if background is true
	});

	const hideClassName = classNames("testing", {
		hideCheckboxes,
	});

	return (
		<div className={ containerClassName }>
			<div className="inner">
				<Slider {...settings}>
					{data.ValuesItems.map((item, index) => (
						<div key={index}>
							<h2 className={textClassName}
							>{item.SectionTitle}</h2>
							<p className={textClassName}
								dangerouslySetInnerHTML={{ __html: item.SectionText }}
							>
							</p>
						</div>
					))}
				</Slider>
			</div>
			<div className={hideClassName}>
				<label className="styled-label">
					<input
						type="checkbox"
						name="italic"
						checked={italic}
						onChange={handleCheckboxChange}
					/>
                    Italic
				</label>
				<label className="styled-label">
					<input
						type="checkbox"
						name="bold"
						checked={bold}
						onChange={handleCheckboxChange}
					/>
                    Bold
				</label>
				<label className="styled-label">
					<input
						type="checkbox"
						name="border"
						checked={border}
						onChange={handleCheckboxChange}
					/>
                    Border
				</label>
				<label className="styled-label">
					<input
						type="checkbox"
						name="background"
						checked={background}
						onChange={handleCheckboxChange}
					/>
                    Background
				</label>
				<label className="styled-label">
					<input
						type="checkbox"
						name="dropShadow"
						checked={dropShadow}
						onChange={handleCheckboxChange}
					/>
                    Drop Shadow
				</label>
				<label className="styled-label">
					<input
						type="checkbox"
						name="hideCheckboxes"
						checked={hideCheckboxes}
						onChange={handleCheckboxChange}
					/>
                    Hide these checkboxes - (refresh page to show again)
				</label>
			</div>
		</div>
	);
};

export default Carousel;